.login{

    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
}

.login > img{
    width: 100%;
}
    
.login > a{
    padding: 20px;
    background-color: #1db954;
    border-radius: 99px;
    font-weight: 700;
    color: white;
    text-decoration: none;
}

.login > a:hover{
    padding: 20px;
    background-color: #5ce47a;
    border-radius: 99px;
    font-weight: 700;
    color: white;
    text-decoration: none;
}









